import React from "react";
import {
  Grid,
  Row,
  InputGroup,
  Input,
  Icon,
  Col,
  Panel,
} from "rsuite";
import { Field } from "react-final-form";

class HomeownersExpenses extends React.Component {
  state = {
    oneTimeExpenses: true,
    homeInsurance_Yr: true,
    propertyTaxes_Yr: true,
  };

  handleChangeOneTimeExpensesPercent = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "oneTimeExpensesPercent",
      (formValues.oneTimeExpenses / formValues.homeValue) * 100
    );
  };

  handleChangeOneTimeExpenses = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "oneTimeExpenses",
      formValues.oneTimeExpensesPercent * 0.01 * formValues.homeValue
    );
  };

  handleChangeHomeInsurance_YrPercent = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "homeInsurance_YrPercent",
      (formValues.homeInsurance_Yr / formValues.homeValue) * 100
    );
  };

  handleChangeHomeInsurance_Yr = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "homeInsurance_Yr",
      formValues.homeInsurance_YrPercent * 0.01 * formValues.homeValue
    );
  };

  handleChangePropertyTaxes_YrPercent = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "propertyTaxes_YrPercent",
      (formValues.propertyTaxes_Yr / formValues.homeValue) * 100
    );
  };

  handleChangePropertyTaxes_Yr = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "propertyTaxes_Yr",
      formValues.propertyTaxes_YrPercent * 0.01 * formValues.homeValue
    );
  };

  toggleField = (field) => () => {
    const oldValue = this.state[field];
    this.setState({ [field]: !!!oldValue });
  };

  render() {
    const { formProps } = this.props;
    return (
      <Panel header="HOMEOWENERS EXPENSES" shaded>
        <Grid fluid gutter={20}>
          <Row>
            <Field
              name="isUsingOneTimeExpense"
              render={(fieldProps) => (
                <input type="hidden" {...fieldProps.input} />
              )}
            />
          </Row>
          <Row gutter={20}>
            <Col xs={12}>
              <label>One-Time Expenses</label>
              <InputGroup>
                {this.props.isusingOnetimeExpense === true && (
                  <Field
                    name="oneTimeExpenses"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeOneTimeExpensesPercent(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                {this.props.isusingOnetimeExpense === false && (
                  <Field
                    name="oneTimeExpensesPercent"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeOneTimeExpenses(formProps)}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={
                    this.props.isusingOnetimeExpense === true ? "primary" : ""
                  }
                  onClick={() => this.props.onToggleHomeownerExpense()}
                >
                  <Icon icon="usd" />
                </InputGroup.Button>
                <InputGroup.Button
                  color={
                    this.props.isusingOnetimeExpense === false ? "primary" : ""
                  }
                  onClick={() => this.props.onToggleHomeownerExpense()}
                >
                  <Icon icon="percent" />
                </InputGroup.Button>
              </InputGroup>
            </Col>
            <Col xs={12}>
              <label>Home Insurance / Year</label>
              <InputGroup>
                {this.state.homeInsurance_Yr === true && (
                  <Field
                    name="homeInsurance_Yr"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeHomeInsurance_YrPercent(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                {this.state.homeInsurance_Yr === false && (
                  <Field
                    name="homeInsurance_YrPercent"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeHomeInsurance_Yr(formProps)}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={this.state.homeInsurance_Yr === true ? "primary" : ""}
                  onClick={this.toggleField("homeInsurance_Yr")}
                >
                  <Icon icon="usd" />
                </InputGroup.Button>
                <InputGroup.Button
                  color={this.state.homeInsurance_Yr === false ? "primary" : ""}
                  onClick={this.toggleField("homeInsurance_Yr")}
                >
                  <Icon icon="percent" />
                </InputGroup.Button>
              </InputGroup>
            </Col>
          </Row>
          <Row gutter={20}>
            <Col xs={12}>
              <label>Property Taxes / Year</label>
              {/* <Field
                name="propertyTaxes/Yr"
                render={(fieldProps) => (
                  <InputGroup>
                    <Input {...fieldProps.input} />
                    <InputGroup.Button>
                      <Icon icon="usd" />
                    </InputGroup.Button>
                    <InputGroup.Button>
                      <Icon icon="percent" />
                    </InputGroup.Button>
                  </InputGroup>
                )}
              /> */}
              <InputGroup>
                {this.state.propertyTaxes_Yr === true && (
                  <Field
                    name="propertyTaxes_Yr"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangePropertyTaxes_YrPercent(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                {this.state.propertyTaxes_Yr === false && (
                  <Field
                    name="propertyTaxes_YrPercent"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangePropertyTaxes_Yr(formProps)}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={this.state.propertyTaxes_Yr === true ? "primary" : ""}
                  onClick={this.toggleField("propertyTaxes_Yr")}
                >
                  <Icon icon="usd" />
                </InputGroup.Button>
                <InputGroup.Button
                  color={this.state.propertyTaxes_Yr === false ? "primary" : ""}
                  onClick={this.toggleField("propertyTaxes_Yr")}
                >
                  <Icon icon="percent" />
                </InputGroup.Button>
              </InputGroup>
            </Col>
            <Col xs={12}>
              <label>HOA Fees / Month</label>
              <Field
                name="hoaFees_Mo"
                render={(fieldProps) => (
                  <InputGroup>
                    <InputGroup.Button>
                      <Icon icon="usd" />
                    </InputGroup.Button>
                    <Input {...fieldProps.input} />
                  </InputGroup>
                )}
              />
            </Col>
          </Row>
        </Grid>
      </Panel>
    );
  }
}

// HomeownersExpenses.propTypes = {
//   classes: PropTypes.object,
// };

export default HomeownersExpenses;
