import React from "react";
import {
  Grid,
  Row,
  InputGroup,
  Input,
  Icon,
  Col,
  Panel,
  DatePicker,
  Button,
  ButtonGroup,
} from "rsuite";
import { Field } from "react-final-form";
import calculator from "../MortgageCalculator/calculator";
import { PAYMENT_FREQUENCY } from './constants';

class MortgageDetails extends React.Component {
  state = {
    downPayment: true,
    pmi_yr: true,
    amortizationPeriod: true,
    paymentFrequency: true,
  };

  handleDisableDate = (startDate) => (date) => {
    return date < startDate;
  };

  handleChangeInputHomeValue = (formProps) => () => {
    const formValues = formProps.form.getState().values;

    this.state.downPayment === true
      ? formProps.form.change(
        "downPaymentPercent",
        calculator.getDownPaymentPercent(formProps.form.getState().values)
      )
      : formProps.form.change(
        "downPayment",
        calculator.getDownPayment(formProps.form.getState().values)
      );

    formProps.form.change(
      "mortgageAmount",
      formValues.homeValue - formProps.form.getState().values.downPayment
    );

    this.state.pmi_yr === true
      ? formProps.form.change(
        "pmi_yrPercent",
        (formValues.pmi_yr /
          formProps.form.getState().values.mortgageAmount) *
        100
      )
      : formProps.form.change(
        "pmi_yr",
        formProps.form.getState().values.mortgageAmount *
        (formValues.pmi_yrPercent * 0.01)
      );

    if (this.props.isusingOnetimeExpense) {
      formProps.form.change(
        "oneTimeExpensesPercent",
        calculator.getOneTimeExpensesPercent(formProps.form.getState().values)
      );
    } else {
      formProps.form.change(
        "oneTimeExpenses",
        calculator.getOneTimeExpense(formProps.form.getState().values)
      );
    }
  };

  handleChangeInputDownPaymentPercent = (formProps) => () => {
    formProps.form.change(
      "downPayment",
      calculator.getDownPayment(formProps.form.getState().values)
    );

    formProps.form.change(
      "mortgageAmount",
      calculator.getMortgageAmount(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yr",
      calculator.getPMIperYear(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yrPercent",
      calculator.getPMIperYearPercent(formProps.form.getState().values)
    );
  };

  handleChangeInputDownPayment = (formProps) => () => {
    formProps.form.change(
      "downPaymentPercent",
      calculator.getDownPaymentPercent(formProps.form.getState().values)
    );

    formProps.form.change(
      "mortgageAmount",
      calculator.getMortgageAmount(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yrPercent",
      calculator.getPMIperYearPercent(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yr",
      calculator.getPMIperYear(formProps.form.getState().values)
    );
  };

  handleChangeInputMortgageAmount = (formProps) => () => {
    formProps.form.change(
      "downPayment",
      calculator.getDownPaymentMort(formProps.form.getState().values)
    );

    formProps.form.change(
      "downPaymentPercent",
      calculator.getDownPaymentPercent(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yrPercent",
      calculator.getPMIperYearPercent(formProps.form.getState().values)
    );

    formProps.form.change(
      "pmi_yr",
      calculator.getPMIperYear(formProps.form.getState().values)
    );
  };

  handleChangePmi_yrPercent = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "pmi_yrPercent",
      (formValues.pmi_yr / formValues.mortgageAmount) * 100
    );
  };

  handleChangePmi_yr = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "pmi_yr",
      formValues.mortgageAmount * (formValues.pmi_yrPercent * 0.01)
    );
  };

  handleChangeAmortizationPeriodYr = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "amortizationPeriodYr",
      formValues.amortizationPeriodYr
    );
  };

  handleChangeAmortizationPeriodMo = (formProps) => () => {
    const formValues = formProps.form.getState().values;
    formProps.form.change(
      "amortizationPeriodMo",
      formValues.amortizationPeriodYr * 12
    );
  };

  toggleField = (field) => () => {
    const oldValue = this.state[field];
    this.setState({ [field]: !!!oldValue });
  };

  render() {
    const { formProps } = this.props;
    const formValues = formProps.form.getState().values;
    return (
      <Panel header="MORTGAGE DETAILS" shaded>
        <Grid fluid>
          <Row gutter={24}>
            <Col xs={12}>
              <label>Home Value</label>
              <Field
                name="homeValue"
                render={(fieldProps) => (
                  <InputGroup>
                    <InputGroup.Button>
                      <Icon icon="usd" />
                    </InputGroup.Button>
                    <Input
                      {...fieldProps.input}
                      onBlur={this.handleChangeInputHomeValue(formProps)}
                    />
                  </InputGroup>
                )}
              />
            </Col>
            <Col xs={12}>
              <label>Down Payment</label>
              <InputGroup>
                {this.state.downPayment === true && (
                  <Field
                    name="downPayment"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeInputDownPayment(formProps)}
                      />
                    )}
                  />
                )}
                {this.state.downPayment === false && (
                  <Field
                    name="downPaymentPercent"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeInputDownPaymentPercent(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={this.state.downPayment === true ? "primary " : ""}
                  onClick={this.toggleField("downPayment")}
                >
                  <Icon icon="usd" />
                </InputGroup.Button>
                <InputGroup.Button
                  color={this.state.downPayment === false ? "primary" : ""}
                  onClick={this.toggleField("downPayment")}
                >
                  <Icon icon="percent" />
                </InputGroup.Button>
              </InputGroup>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col xs={12}>
              <label>Mortgage Amount</label>
              <Field
                name="mortgageAmount"
                render={(fieldProps) => (
                  <InputGroup>
                    <InputGroup.Button>
                      <Icon icon="usd" />
                    </InputGroup.Button>
                    <Input
                      {...fieldProps.input}
                      onBlur={this.handleChangeInputMortgageAmount(formProps)}
                    />
                  </InputGroup>
                )}
              />
            </Col>
            <Col xs={12}>
              <label>Interest Rate</label>
              <Field
                name="interestRate"
                render={(fieldProps) => (
                  <InputGroup>
                    <InputGroup.Button>
                      <Icon icon="percent" />
                    </InputGroup.Button>
                    <Input {...fieldProps.input} />
                  </InputGroup>
                )}
              />
            </Col>
          </Row>
          <Row gutter={24}>
            <Col xs={12}>
              <label>PMI / Year</label>
              <InputGroup>
                {this.state.pmi_yr === true && (
                  <Field
                    name="pmi_yr"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangePmi_yrPercent(formProps)}
                      />
                    )}
                  />
                )}
                {this.state.pmi_yr === false && (
                  <Field
                    name="pmi_yrPercent"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangePmi_yr(formProps)}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={this.state.pmi_yr === true ? "primary" : ""}
                  onClick={this.toggleField("pmi_yr")}
                >
                  <Icon icon="usd" />
                </InputGroup.Button>
                <InputGroup.Button
                  color={this.state.pmi_yr === false ? "primary" : ""}
                  onClick={this.toggleField("pmi_yr")}
                >
                  <Icon icon="percent" />
                </InputGroup.Button>
              </InputGroup>
            </Col>
            <Col xs={12}>
              <label>Amortization Period</label>
              <InputGroup>
                {this.state.amortizationPeriod === true && (
                  <Field
                    name="amortizationPeriodYr"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeAmortizationPeriodMo(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                {this.state.amortizationPeriod === false && (
                  <Field
                    name="amortizationPeriodMo"
                    render={(fieldProps) => (
                      <Input
                        {...fieldProps.input}
                        onBlur={this.handleChangeAmortizationPeriodYr(
                          formProps
                        )}
                      />
                    )}
                  />
                )}
                <InputGroup.Button
                  color={
                    this.state.amortizationPeriod === true ? "primary" : ""
                  }
                  onClick={this.toggleField("amortizationPeriod")}
                >
                  <label
                    style={
                      this.state.amortizationPeriod === true
                        ? { color: "white" }
                        : { color: "grey" }
                    }
                  >
                    Yr
                  </label>
                </InputGroup.Button>
                <InputGroup.Button
                  color={
                    this.state.amortizationPeriod === false ? "primary" : ""
                  }
                  onClick={this.toggleField("amortizationPeriod")}
                >
                  <label
                    style={
                      this.state.amortizationPeriod === false
                        ? { color: "white" }
                        : { color: "grey" }
                    }
                  >
                    Mo
                  </label>
                </InputGroup.Button>
              </InputGroup>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col xs={12}>
              <label>Payment Frequency</label>
              <Field
                name="paymentFrequency"
                render={(fieldProps) => (
                  <ButtonGroup justified>
                    <Button
                      appearance={
                        fieldProps.input.value === PAYMENT_FREQUENCY.MONTHLY ? "primary" : ""
                      }
                      value={PAYMENT_FREQUENCY.MONTHLY}
                      onClick={() => fieldProps.input.onChange(PAYMENT_FREQUENCY.MONTHLY)}
                    >
                      Monthy
                    </Button>
                    <Button
                      appearance={
                        fieldProps.input.value === PAYMENT_FREQUENCY.BIWEEKLY ? "primary" : ""
                      }
                      onClick={() => fieldProps.input.onChange(PAYMENT_FREQUENCY.BIWEEKLY)}
                    >
                      Bi-weekly
                    </Button>
                  </ButtonGroup>
                )}
              />
            </Col>

            <Col xs={12}>
              <label>Start Date</label>
              <Field
                name="startDate"
                render={(fieldProps) => (
                  <InputGroup>
                    <DatePicker
                      oneTap
                      style={{ width: 500 }}
                      disabledDate={this.handleDisableDate(
                        formValues.startDate
                      )}
                      {...fieldProps.input}
                    />
                  </InputGroup>
                )}
              />
            </Col>
          </Row>
        </Grid>
      </Panel>
    );
  }
}

export default MortgageDetails;
